body {
  font-family: 'Arial', sans-serif;
  background-color: #f2f2f2;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.App {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 600px;
}

/* Title */
h1 {
  text-align: center;
  color: #ff69b4;
  font-size: 2em;
  margin-bottom: 20px;
}

/* Forms */
form {
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button.add-order-btn,
button.cancel-btn {
  background-color: #ff69b4;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
}

button.cancel-btn {
  background-color: #ccc;
  margin-left: 10px;
}

button.add-order-btn:hover,
button.cancel-btn:hover {
  background-color: #ff1493;
}

button.cancel-btn:hover {
  background-color: #bbb;
}

/* Order list */
h2 {
  margin-top: 20px;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #f9f9f9;
}

li.confirmed {
  border-color: #00ff00;
}

li.not-confirmed {
  border-color: #ff0000;
}

li ul {
  margin-top: 10px;
}

li div {
  margin-top: 10px;
}

button.confirm-btn,
button.delete-btn,
button.add-cake-to-order-btn {
  margin-right: 10px;
}

button.confirm-btn {
  background-color: #4CAF50;
  color: white;
}

button.confirm-btn:disabled {
  background-color: #aaa;
}

button.delete-btn {
  background-color: #f44336;
  color: white;
}

button.add-cake-to-order-btn {
  background-color: #ff69b4;
  color: white;
}

/* Client list */
.clients-list {
  margin-top: 20px;
}

button.view-clients-btn,
button.hide-clients-btn,
button.auth-btn,
button.format-orders-btn {
  background-color: #ff69b4;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  margin-bottom: 10px;
}

button.hide-clients-btn {
  background-color: #ccc;
}

/* Admin controls */
.admin-controls {
  margin-top: 20px;
}

button.update-price-btn,
button.add-flavor-btn,
button.remove-flavor-btn,
button.logout-btn {
  background-color: #ff69b4;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 10px;
  margin-bottom: 10px;
}

button.logout-btn {
  background-color: #ccc;
}